html * {
  outline: px solid red;
}
.search {
display: flex;
}
.search_icon {
width: 20px;
height: 20px;
display: flex;
border: 1px solid grey;
}
.search_cont {
  width: 0;
  box-sizing: border-box;
  overflow: hidden;
  transition: all .3s ease;
}
.search_icon:hover+ .search_cont {
  width: 300px;
  transition: all .3s ease;
  
}
.pole {
  height: 50px;
}


